import axios, { AxiosInstance, AxiosRequestConfig } from "axios";
import { API_HUMANIQ_URL } from "../constants/api";
import { formatRoute } from "../utils/network";

export class ApiService {
  axios: AxiosInstance
  init = (baseURL: string = API_HUMANIQ_URL, headers = {}) => {
    this.axios = axios.create({ baseURL, headers })
  }
  get = async (path: string, params?: any, config?: AxiosRequestConfig) => this.axios.get(formatRoute(path, params), config)
  post = async (path: string, body?: any, params?: any, config?: AxiosRequestConfig) => this.axios.post(params ? formatRoute(path, params) : path, body, config)
}